@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.App {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(270deg, #ECF8E8, #edf2ff);
}
.title {
  font-size: 55px;
  font-weight: bolder;
}
.sub_title {
  font-size: 24px;
}
.search_container {
  display: flex;
  align-items: center;
  flex-direction: column;
  font-family: "Nunito", sans-serif;
  
}
.searchInputs {
  margin-top: 105px;
  display: flex;
  justify-content: center;
    width: 100%;
}
.search {
  width: 100%;
}
/* .searchContainer {
  display: flex;
    justify-content: center;
} */
.search input {
  background-color: white;
  border: 0;
  border-radius: 2px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  font-size: 18px;
  padding: 15px;
  
  width: 80%;
}

.searchIcon {
  height: 60px;
  width: 50px;
  background-color: white;
  display: grid;
  place-items: center;
}

input:focus {
  outline: none;
}
.searchIcon svg {
  font-size: 35px;
}

.dataresultcontainer {
  display: flex;
  justify-content: center;
}

.dataResult {
  margin-top: 5px;
  width: 70%;
  height: 200px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  overflow: hidden;
  overflow-y: auto;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
}

.dataResult::-webkit-scrollbar {
  display: none;
}

.dataResult .dataItem {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  color: #ff4747;
  font-weight: bold;
  cursor: pointer;
}
.dataItem:hover {
  background-color: #ECF8E8;
}
.dataItem p {
  margin-left: 10px;
}
a {
  text-decoration: none;
}

a:hover {
  background-color: lightgrey;
}

#clearBtn {
  cursor: pointer;
}
.nav {
  background-color: #edf2ff;
  padding: 15px 12%;
}

.nav_title {
  font-size: 30px;
  font-weight: bolder;
}

.stockdatacontainer {
  background: white;
  border-radius: 10px;
  padding: 30px 40px;
  width: 70%;
}
.stockdatacontainerTitle {
  font-size: 30px;
  font-weight: bold;
  padding: 0px 25px
}
.stockdetailblock {
  display: flex;
  justify-content: space-between;
  padding: 10px 15px;
  margin-right: 30px;
  border-radius: 10px;
}
.stockdetailblockTitle {
  color: grey;
}
.stockdetailblockDescription {
  color: #cb1515;
    font-weight: bold;
}
.stockinfoblock {
  display: flex;
  margin-top: 2%; 
  background: #f4f4f4;
  padding: 15px 3%; 
}

.stockdetailblock:nth-child(even) {background-color: #ECF8E8;}
 
